import Vue from 'vue'
import VueRouter from 'vue-router';
import {routes} from './router';
import App from './App.vue'
import VCalendar from 'v-calendar';
Vue.use(VueRouter);
const router = new VueRouter({
  routes,
  mode: 'history'
});
Vue.use(VCalendar, {
  componentPrefix: 'vc', 
           
});
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
